@import url(https://fonts.googleapis.com/css?family=Josefin+Slab:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  width: 100vw;
  height: 100%;
}

@media (min-width: 1440px) {
  .app {
    height: 100vh;
  }
}

.main {
  flex-grow: 1;
  height: 100vh;
}

h1 {
  margin: 0;
}

.tether-element {
  z-index: 10000;
}

@media (min-width: 320px) {
  .tether-element {
    margin-top: -300px;
  }
}

@media (min-width: 375px) {
  .tether-element {
    margin-top: 130px;
  }
}

.calendarContainer .heading {
  margin-top: -5px;
}

.calendarContainer button {
  background-color: #56f2f8;
  margin-top: -10px;
}

@media (min-width: 1024px) {
  .calendarContainer button {
    margin-top: -15px;
  }
}

@media (min-width: 1440px) {
  .calendarContainer button {
    margin-top: -2px;
  }
}

.iconArrow {
  right: 9%;
  position: absolute;
}

.makeStyles-list-39  > li {
  padding-left: 0;
}
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2%;
  height: 22vh;
}

@media screen and (min-width: 1024px) {
  .form {
    height: 20vh;
  }
}

@media screen and (min-width: 1440px) {
  .form {
    height: 18vh;
  }
}

.list {
  margin: auto;
  overflow: hidden;
  overflow-y: auto;
  margin-left: 0px;
  max-height: 52vh;
  word-break: break-all;
  margin-top: 18px;
}

@media (min-width: 768px) {
  .list {
    height: 48vh;
    width: 45vw;
  }
}

@media (min-width: 1024px) {
  .list {
    width: 45vw;
    margin-top: 30px;
    height: 45vh;
  }
}

@media (min-width: 1440px) {
  .list {
    margin-left: 3%;
    width: 35vw;
    margin-top: 30px;
  }
}

/* width */
::-webkit-scrollbar {
  width: 4px;
}

@media (min-width: 1440px) {
  ::-webkit-scrollbar {
    width: 6px;
  }
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #aaa;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 10px;
}

.task-input {
  padding: 15px 10px 10px 20px;
  font-family: 'Josefin Sans', serif;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 15px;
  color: #ccc;
  background-color: rgb(82, 82, 82);
  border: none;
  outline: none;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  min-width: 60vw;
}

@media screen and (min-width: 768px) {
  .task-input {
    min-width: 35vw;
  }
}

@media screen and (min-width: 1024px) {
  .task-input {
    min-width: 30vw;
  }
}

@media screen and (min-width: 1440px) {
  .task-input {
    min-width: 25vw;
  }
}

.btn {
  margin: 14px;
}

.add-task-btn {
  min-width: 80px;
  background-color: #717171;
  color: #ccc;
  border: none;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  outline: none;
  cursor: pointer;
}

@media screen and (min-width: 1440px) {
  .add-task-btn {
    min-width: 100px;
    font-size: 20px;
  }
}

.list-item {
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
  font-size: 20px;
  margin: 0px 0px 0px -30px;
  align-items: center;
  height: 10vh;
}

@media (min-width: 1024px) {
  .list-item {
    height: 8vh;
  }
}

@media (min-width: 1440px) {
  .list-item {
    font-size: 25px;
  }
}

.task-btn {
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  width: 35px;
}

.list-task {
  display: flex;
  flex-direction: row;
}

.btn-delete i,
.btn-edit i {
  font-size: 16px;
  margin-right: 20px;
}

@media (min-width: 1024px) {
  .btn-delete i,
  .btn-edit i {
    font-size: 18px;
  }
}

@media (min-width: 1440px) {
  .btn-delete i,
  .btn-edit i {
    font-size: 20px;
  }
}

.no-tasks {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .no-tasks {
    height: 55vh;
    margin-right: 100px;
    margin-top: 0px;
  }
}

@media (min-width: 1024px) {
  .no-tasks {
    height: 50vh;
    margin-right: 150px;
  }
}

@media (min-width: 1440px) {
  .no-tasks {
    font-size: 35px;
    margin-right: 80px;
  }
}

